import helper from '../../common/helper.js';
export default {
	data() {
		return {
			array:'',
			ispws:'',
			Phonetext:''
		};
	},
	onLoad(){
		this.Isphone()
	},
	onShow(){
		this.Isphone()
	},
	methods: {
		// 判断用户是否绑定手机号和用户是否有密码、
		Isphone() {
			uni.request({
				url: helper.websiteUrl+'usercenter/account',
				method: 'GET',
				data: helper.postdata({
					"id": helper.getstate().userid,
				}),
				success: res => {
					if (res.data.code == 200) {
						this.array = res.data.data.baseinfo.isphone 
						this.ispws = res.data.data.baseinfo.ispw
					} else{
						
					}
				},
				fail: () => {},
			});
		},
		// 绑定手机号
		Fillphone(){
			uni.navigateTo({
				url: 'Tiephone',
			})
		},
		openxiugai() { //打开修改手机号
			uni.navigateTo({
				url: 'xiugaiphone',
			});
		},
		amendpassword() { //打开修改密码
			uni.navigateTo({
				url: 'xiugaipassword',
			});
		},
		Fillgpassword() { //打开填写密码
			uni.navigateTo({
				url: 'tianxiepassword',
			});
		}

	}
}
